<template>
    <div class="homeView">
        <el-container>
            <el-header>
                <div class="logo">
                    <img class="img" src="@/assets/images/logo2.png" alt="">
                    <h3 class="text">数据分析·管理系统</h3>
                </div>
                <div class="logout">
                    <el-popconfirm title="确定要退出登录吗？" @confirm="logout">
                        <el-button type="danger"  slot="reference">退出登录</el-button>
                    </el-popconfirm>
                </div>
            </el-header>
            <el-container class="homeContent">
                <el-aside :width="isCollapse?'64px':'150px'">
                    <div class="collapse" @click="clickFold">
                        <i class="el-icon-d-arrow-left" v-show="isCollapse"></i>
                        <i class="el-icon-d-arrow-right" v-show="!isCollapse"></i>
                    </div>
                    <el-menu
                    background-color="#304156"
                    text-color="rgba(255,255,255,0.6)"
                    active-text-color="#fff"
                    :collapse="isCollapse"
                    :collapse-transition="isTransition"
                    router
                    :default-active="$route.path"
                    >
                    <el-menu-item index="/alluser">
                        <i class="el-icon-coin"></i>
                        <span slot="title">意向客户</span>
                    </el-menu-item>

                    <el-menu-item index="/adduser">
                        <i class="el-icon-user"></i>
                        <span slot="title">添加客户</span>
                    </el-menu-item>

                    <el-menu-item index="/userdata">
                        <i class="el-icon-data-analysis"></i>
                        <span slot="title">数据分析</span>
                    </el-menu-item>

                    </el-menu>
                </el-aside>
                <el-main>
                    <router-view></router-view>
                </el-main>
            </el-container>
        </el-container>
    </div>
</template>

<script>
export default {
  data () {
    return {
      isCollapse: true,
      isTransition: false
    }
  },
  methods: {
    logout () {
      window.sessionStorage.removeItem('token')
      this.$router.push('/login')
      this.$message('退出登录成功！')
    },
    clickFold () {
      this.isCollapse = !this.isCollapse
    }
  }
}
</script>

<style lang="less" scope>
.homeView{
    height:100%;
}
.el-header{
    background: #17253F;
    display: flex;
    justify-content: space-between;
    height: 100%;
    text-align: center;
    align-items: center;
    .logo{
        display: flex;;
        justify-content: space-between;
        .img{
            height: 60%;
        }
        .text{
            margin-left: 20px;
            margin-top: 12px;
            font-weight: 200;
            align-items: center;
            color:rgba(255, 255, 255, 0.6)
        }
    }
}
.el-aside{
    background: #304156;
    position: relative;
    .el-menu{
        border-right: none;
    }
    .collapse{
        width: 20px;
        line-height:60px;
        color:#ccc;
        background: #17253F;
        position: absolute;
        align-items: center;
        font-size:15px;
        right:0;
        top:calc(50% - 30px);
        padding-left: 3px;
        box-sizing: border-box;
    }

}
.el-container{
    height: 100%;
    background: #F8F8F8;
}
.homeContent{
    height: calc(100% - 60px);
}
</style>
